@import url('https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Balsamiq Sans', cursive;

}

Clock {
  color: black;
}

.main_div {
  width: 100%;
  height: 100vh;
  background-color: darksalmon;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div {
  width: 20%;
  height: 50vh;
  background-color: aliceblue;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
 
  
  
}

h1 {
  color: white;
  background: transparent;
  background-color: teal;
  /* padding: 5px 5px 5px 5px; */
  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);
  margin-bottom: 50px;
  padding: 6px 0 2px 0;
  width: 100%;
  font-size: 50px;
  
  
  
  
  /* text-align: center; */
}

button {

  min-height: 40px ;
  border-radius: 5%;
  border-color: transparent;
  color: #fff;
  font-size: 20px;
  border: none;
  outline: none;
  padding: 0 15px;
  margin-top: 30px;

  background-color: teal;font-weight: 500;

  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);

}

button:hover {
  background-color: cadetblue;
}

.btn_div {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.btn-green:hover {
  color: green;
}


.btn-red:hover {
  color: red;
}